import noMore from '@/components/noMore.vue'
import loadErr from '@/components/loadErr.vue'

export default {
	data() {
		return {
			typeArr: [{
				title: '全部',
				type: ''
			}, {
				title: '线路',
				type: 1
			}, {
				title: '商品',
				type: 2
			}],
			selectType: '', //默认为1
			isLoadErr: false, //加载失败
			
			all_index: 1,
			all_size: 10,
			all_dataList: [],
			all_isMore: true,
			
			line_index: 1,
			line_size: 10,
			line_dataList: [],
			line_isMore: true,
			
			pro_index: 1,
			pro_size: 10,
			pro_dataList: [],
			pro_isMore: true
			
		}
	},
	components: { noMore, loadErr },
	onLoad() {
		this.getAllList() //默认加载
	},
	methods: {
		toJumpDetail(idx) {
			const $this = this
			if(this.selectType == '') {
				if(this.all_dataList[idx].type == 1) {
					uni.navigateTo({
						url:`/pages/lineDetail/lineDetail?id=${$this.all_dataList[idx].third_id}`
					})
				}else if(this.all_dataList[idx].type == 2) {
					uni.navigateTo({
						url:`/pages/outdoorsPro/outProDetail?id=${$this.all_dataList[idx].third_id}`
					})
				}
			}else if(this.selectType == 1) {
				uni.navigateTo({
					url:`/pages/lineDetail/lineDetail?id=${$this.line_dataList[idx].third_id}`
				})
			}else if(this.selectType == 2) {
				uni.navigateTo({
					url:`/pages/outdoorsPro/outProDetail?id=${$this.pro_dataList[idx].third_id}`
				})
			}
		},
		toRelaodData() {
			this.isLoadErr = false
			if(this.selectType == '') {
				this.all_index == 1
				this.all_isMore = true
				this.all_dataList = []
				this.getAllList()
			}else if(this.selectType == 1) {
				this.line_index == 1
				this.line_isMore = true
				this.line_dataList = []
				this.getLineList()
			}else if(this.selectType == 2) {
				this.pro_index == 1
				this.pro_isMore = true
				this.pro_dataList = []
				this.getProList()
			}
		},
		toLoadMore() {
			if(this.selectType == '') {
				if(this.all_isMore) {
					this.getAllList()
				}
			}else if(this.selectType == 1) {
				if(this.line_isMore) {
					this.getLineList()
				}
			}else if(this.selectType == 2) {
				if(this.pro_isMore) {
					this.getProList()
				}
			}
		},
		toCutCollectType(idx) {
			this.typeArr.map((v, i) => {
				if(idx == i) {
					this.selectType = v.type
					if(v.type == '' && this.all_dataList.length == 0 && this.all_isMore) {
						this.getAllList()
					}else
					if(v.type == 1 && this.line_dataList == 0 && this.line_isMore) {
						this.getLineList()
					}else
					if(v.type == 2 && this.pro_dataList == 0 && this.pro_isMore) {
						this.getProList()
					}
				}
			})
		},
		getAllList() { //获取全部列表
			if(this.all_index == 1) {
				uni.showLoading({
					title: '数据加载中',
					mask: true
				})
			}
			this.$api.ajax({
				url: this.$config.collectList,
				data: {
					index: this.all_index,
					size: this.all_size,
					type: this.selectType
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					arr.map(v => {
						v.infoObj = JSON.parse(v.third_json)
					})

					if(arr.length < this.all_size) {
						this.all_dataList = this.all_dataList.concat(arr)
						this.all_isMore = false //没有更多了
					}else {
						this.all_dataList = this.all_dataList.concat(arr)
						this.all_index++
					}
					setTimeout(() => {
						uni.hideLoading();
					}, 500)
				}else {
					if(this.all_index == 1) {
						this.isLoadErr = true
					}else {
						this.$api.warnNotice(res.data.message)
					}
					uni.hideLoading();
				}
			})
			.catch((err) => {
				if(this.all_index == 1) {
					this.isLoadErr = true
				}else {
					this.$api.warnNotice('列表加载失败')
				}
				uni.hideLoading();
			})
		},
		getLineList() { //获取线路列表
			if(this.line_index == 1) {
				uni.showLoading({
					title: '数据加载中',
					mask: true
				})
			}
			this.$api.ajax({
				url: this.$config.collectList,
				data: {
					index: this.line_index,
					size: this.line_size,
					type: this.selectType
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					arr.map(v => {
						v.infoObj = JSON.parse(v.third_json)
					})
					
					if(arr.length < this.line_size) {
						this.line_dataList = this.line_dataList.concat(arr)
						this.line_isMore = false //没有更多了
					}else {
						this.line_dataList = this.line_dataList.concat(arr)
						this.line_index++
					}
					setTimeout(() => {
						uni.hideLoading();
					}, 500)
				}else {
					if(this.line_index == 1) {
						this.isLoadErr = true
					}else {
						this.$api.warnNotice(res.data.message)
					}
					uni.hideLoading();
				}
			})
			.catch((err) => {
				if(this.line_index == 1) {
					this.isLoadErr = true
				}else {
					this.$api.warnNotice('列表加载失败')
				}
				uni.hideLoading();
			})
		},
		getProList() { //获取线路列表
			if(this.pro_index == 1) {
				uni.showLoading({
					title: '数据加载中',
					mask: true
				})
			}
			this.$api.ajax({
				url: this.$config.collectList,
				data: {
					index: this.pro_index,
					size: this.pro_size,
					type: this.selectType
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					arr.map(v => {
						v.infoObj = JSON.parse(v.third_json)
					})
		
					if(arr.length < this.pro_size) {
						this.pro_dataList = this.pro_dataList.concat(arr)
						this.pro_isMore = false //没有更多了
					}else {
						this.pro_dataList = this.pro_dataList.concat(arr)
						this.pro_index++
					}
					setTimeout(() => {
						uni.hideLoading();
					}, 500)
				}else {
					if(this.pro_index == 1) {
						this.isLoadErr = true
					}else {
						this.$api.warnNotice(res.data.message)
					}
					uni.hideLoading();
				}
			})
			.catch((err) => {
				if(this.pro_index == 1) {
					this.isLoadErr = true
				}else {
					this.$api.warnNotice('列表加载失败')
				}
				uni.hideLoading();
			})
		}
	}
}
